<template>
	<div class="news">
		<div id="content">
			<!-- 轮播图开始 -->
			<Horselight></Horselight>
			<!-- 轮播图结束 -->
			<!-- 动态开始 -->
			<div class="trend">
				<div class="tags">
					<ul>
						<li v-for="(item,index) in newsList" :key=index :class="active == index ? 'active' : '' " @click="addClass(index)">
							<router-link :to="item.pathUrl">
								{{item.title}}
							</router-link>
						</li>
					</ul>
				</div>
				<!-- 动态结束 -->
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
	import Horselight from '@/components/v-horselight'
	import developmentNews from '@/components/developmentNews'
	import enterpriseNews from '@/components/enterpriseNews'
	import industryNews from '@/components/industryNews'
	export default {
		name: 'News',
		data(){
			return{
				newsList:[
					{id:1,pathUrl:"/news/5/industryNews",title:"行业动态"},
					{id:2,pathUrl:"/news/5/enterpriseNews",title:"企业动态"},
					{id:3,pathUrl:"/news/5/developmentNews",title:"开发动态"}
				],
				active:0
			}
		},
		components: {
			Horselight,
			developmentNews,
			enterpriseNews,
			industryNews
		},
		methods:{
			addClass(index) {
				this.active = index
			},
		},
		mounted() {
		}
		
	}
	
</script>

<style scoped>
	@import url("../assets/css/news.css");
</style>
