<template>
	<div class="developmentNews">
				<el-row :gutter="20" style="max-width: 1000px;margin: 0 auto;" >
					<el-col v-for="development in developmentList" :key="development.id" class="newsChild" :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
						<img :src="development.pics[0].url">
						<div class="font">
							<h3>{{development.newsTitle | ellipsis(15)}}</h3>
							<h4>{{development.newsContent}}</h4>
							<p>{{development.newsSource}}</p>
							<p>{{development.publicTime}}</p>
						</div>
					</el-col>
				</el-row>
				
				<!-- 分页开始 -->
				<div class="paging">
					<el-pagination 
					@current-change="usecaseMethods"
					:page-size="14"
					:total="total"
					layout="prev, pager, next" background>
					</el-pagination>
				</div>
				<!-- 分页结束 -->
				
	</div>
</template>

<script>
	export default {
		name: 'developmentNews',
		components: {
		},
		data() {
			return {
				developmentList: null,
				total:0,
			}
		},
		methods: {
			postList() {
				this.$http.post(process.env.VUE_APP_URL+'/news/selectType',{'type':3,'page':1,'size':14},{emulateJSON:true}).then(function(data) {
					this.developmentList = data.body.data.records; 
					this.total=data.body.data.total;
				});
		
			},
			usecaseMethods(page){
				 this.$http.post(process.env.VUE_APP_URL+'/news/selectType',{'type':3,'page':page,'size':14},{emulateJSON:true}).then(function(data) {
				 	this.developmentList = data.body.data.records;
				 	window.console.log(data.body.data);
				 });
			}
		},
		mounted: function() {
			this.postList();
			
		},
	}
</script>

